module.exports = ".content-div {\n  background: #fff;\n  border-radius: 50px 50px 0px 0px;\n  top: -50px;\n  position: relative;\n  width: 100%;\n}\n\n.empty-div {\n  background: #0A15D5;\n  height: 120px;\n}\n\n.custom-icon-project-list-icon {\n  font-size: 25px;\n}\n\n.custom-icon-calendar-icon,\n.custom-icon-chat-icon-1 {\n  font-weight: bold;\n}\n\n.project-list {\n  background: none;\n  border-radius: 50px;\n  padding: 0;\n  margin: 10px 30px 10px 50px;\n  box-shadow: -7px 10px 18px 7px rgba(0, 0, 0, 0.14);\n}\n\n.project-list .project-content {\n  padding: 10px 20px 10px 30px;\n}\n\n.project-name {\n  font-size: 14px;\n  font-weight: bold;\n}\n\n.head-title {\n  color: #fff;\n  font-size: 16px;\n  font-weight: bold;\n}\n\n.pro-text {\n  font-size: 12px;\n  font-weight: bold;\n}\n\n.progress-div {\n  width: 80%;\n  background: #CBF4DB;\n  height: 70px;\n}\n\n.project-content {\n  position: absolute;\n  top: 0;\n  width: 100%;\n  height: 70px;\n}\n\n.sort-item {\n  width: 80px;\n  border-bottom: solid 1.5px #FE9C45;\n  margin: 10px;\n  padding: 0;\n  --padding-start: 0;\n  margin: 10px 0px 10px 50px;\n}\n\n.sort-item ion-label {\n  font-size: 20px;\n  font-weight: bold;\n}\n\n.sort-item span {\n  font-size: 18px;\n}\n\n.fab-add-btn {\n  --border-radius: 15px !important;\n  --background: #FE9C45;\n}\n\n.timeline {\n  position: relative;\n  max-width: 100%;\n  margin: 0 auto;\n  padding-top: 20px;\n}\n\n/* The actual timeline (the vertical ruler) */\n\n.timeline::after {\n  content: \"\";\n  position: absolute;\n  width: 2px;\n  background-color: #D2D2D2;\n  top: 30px;\n  bottom: 0;\n  left: 50%;\n}\n\n/* Container around content */\n\n.container {\n  position: relative;\n  background-color: inherit;\n  padding: 5px 0px;\n}\n\n/* The circles on the timeline */\n\n.container::after {\n  content: \"\";\n  position: absolute;\n  width: 15px;\n  height: 15px;\n  top: 42px;\n  border-radius: 50%;\n  z-index: 1;\n  margin-left: 9px;\n}\n\n.project-content ion-col {\n  white-space: nowrap;\n  overflow: hidden;\n}\n\n.container:nth-of-type(odd)::after {\n  background-color: #0A15D5;\n}\n\n.container:nth-of-type(even)::after {\n  background-color: #FF9F55;\n}\n\n/* Place the container to the left */\n\n.left {\n  left: 0;\n}\n\n/* Place the timelime to the left */\n\n.timeline::after {\n  left: 31px;\n}\n\n/* Make sure all circles are at the same spot */\n\n.left::after,\n.right::after {\n  left: 15px;\n}\n\n.between.days-btn {\n  background: #fe9c45 !important;\n}\n/*# sourceMappingURL=data:application/json;base64, */"